import db from '@/js_sdk/uni-clientDB/index.js'
const dbCmd = db.command
var api = {};

api.getlist = function(option) {
	uniCloud.callFunction({
		name: 'uni-clientDB',
		data: {
			command: db.collection('goods').where({

			}).limit(10).get()
		},
		success(res) {
			console.log(res);
		},
		fail(err) {
			console.error(err)
			uni.showModal({
				content: err.message || '',
				showCancel: false,

			})
		}
	})
}


